.content-1 {
  margin-left: 15%;
  margin-right: 15%;
  margin-top: 50px;
}
.main-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #333;
  text-align: center;
  margin-top: 20px;
}
.first-row {
  text-align: center;
}
.img-fluid {
  width: 100%;
  height: 500px;
  object-fit: contain; /* hoặc scale-down */
  background-color: #f8f8f8; /* tránh nền trắng loá nếu ảnh nhỏ */
  border-bottom: 1px solid #eee;
}
.news-html {
  font-size: 1rem;
  line-height: 1.6;
  text-align: justify;
  padding: 1rem 0;
}

.news-html img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 1rem auto;
}

.news-html h1,
.news-html h2,
.news-html h3 {
  margin-top: 1.5rem;
  color: #2c3e50;
}
