.table-wrapper {
  width: 100%;
  overflow: hidden; /* ❌ KHÔNG scroll ngang */
  border: 1px solid #dee2e6;
}

.table {
  width: 100%;
  table-layout: auto; /* ✅ Co giãn theo nội dung */
  border-collapse: collapse;
}

.table thead th.sticky-header {
  position: sticky;
  top: 0;
  background-color: #f8f9fa;
  z-index: 2;
  text-align: left;
  padding: 12px;
  border-bottom: 2px solid #dee2e6;
}

.table th,
.table td {
  padding: 12px;
  border: 1px solid #dee2e6;
  white-space: normal; /* ✅ Cho phép xuống dòng nếu dài */
  word-break: break-word; /* ✅ Ngắt từ nếu dài */
  max-width: 100%; /* Không cho vượt khung */
}
