/* SearchArtikel.css */

.artikel-container {
  max-width: 900px;
  margin: 120px auto 40px auto;
  padding: 20px;
  background-color: #1a1a1a;
  border-radius: 12px;
  color: #f1f1f1;
  box-shadow: 0 4px 20px rgba(255, 215, 0, 0.2);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.artikel-form {
  margin-bottom: 30px;
}

.search-input {
  border-radius: 8px 0 0 8px;
  background-color: #333;
  border: none;
  color: #fff;
}

.search-input::placeholder {
  color: #bbb;
}

.search-button {
  border-radius: 0 8px 8px 0;
  background-color: #ffd700;
  color: #000;
  font-weight: bold;
  transition: 0.3s ease;
  border: none;
}

.search-button:hover {
  background-color: #ffcc00;
}

.spinner-border {
  width: 3rem;
  height: 3rem;
}
.form-label {
  font-weight: bold;
  font-size: 1.1rem;
  color: white;
  letter-spacing: 0.5px;
  margin-bottom: 6px;
  display: inline-block;
}
.table-scroll-container {
  max-height: 500px;
  overflow-y: auto;
  border: 1px solid #ddd;
  border-radius: 6px;
  margin-top: 1rem;
  padding-right: 10px;
}
